"use client";

import {
  ComposedChart,
  Area,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  CartesianGrid,
} from "recharts";

const data = [
  { date: "May 01", thisMonth: 4000, lastMonth: 2400 },
  { date: "May 05", thisMonth: 3200, lastMonth: 2800 },
  { date: "May 10", thisMonth: 2800, lastMonth: 3000 },
  { date: "May 15", thisMonth: 3600, lastMonth: 2600 },
  { date: "May 20", thisMonth: 3900, lastMonth: 2700 },
  { date: "May 25", thisMonth: 3700, lastMonth: 2500 },
  { date: "May 30", thisMonth: 4200, lastMonth: 2600 },
];

export default function SavingSummary() {
  return (
    <div className="p-4 md:p-8">
        <Sidebar />
<div className="bg-white rounded-2xl shadow-md p-4 md:p-6 w-full md:max-w-4xl ml-auto">
        {/* Header */}
        <div className="flex items-center justify-between">
          <h2 className="text-lg font-semibold">Saving Summary</h2>
          <div className="text-gray-500 text-sm flex items-center gap-1">
            Mar 2022 <span>▼</span>
          </div>
        </div>

        <div className="mt-4 h-64 md:h-60 relative">
          {/* Legend (top-right, screenshot style) */}
          <div className="absolute right-4 top-2 z-10 flex items-center gap-6 text-sm">
            <span className="flex items-center gap-2">
              <span className="inline-block w-4 h-2 rounded bg-teal-600" />
              This month
            </span>
            <span className="flex items-center gap-2 text-gray-500">
              <span className="inline-block w-4 h-2 rounded bg-gray-300" />
              Same period last month
            </span>
          </div>

          <ResponsiveContainer width="100%" height="100%">
            <ComposedChart
              data={data}
              margin={{ top: 20, right: 20, left: 0, bottom: 0 }}
            >
              <defs>
                {/* Teal gradient: top darker → bottom fade */}
                <linearGradient id="tealFill" x1="0" y1="0" x2="0" y2="1">
  <stop offset="0%" stopColor="#0f766e" stopOpacity="0.6" />   {/* upar thoda dark */}
  <stop offset="70%" stopColor="#0f766e" stopOpacity="0.25" /> {/* beech mein visible */}
  <stop offset="100%" stopColor="#0f766e" stopOpacity="0" />   {/* neeche fade out */}
</linearGradient>

              </defs>

              <CartesianGrid stroke="#e5e7eb" strokeDasharray="3 3" vertical />
              <XAxis dataKey="date" tickLine={false} axisLine={false} />
              <YAxis
                domain={[0, 6000]}                       // screenshot-like headroom
                ticks={[0, 500, 1500, 3000, 4500, 6000]} // labels like the image
                tickLine={false}
                axisLine={false}
                tickFormatter={(v) => `$${v}`}
              />
              <Tooltip
                formatter={(v) => [`$${v}`, ""]}
                contentStyle={{
                  borderRadius: 10,
                  border: "none",
                  boxShadow: "0 2px 8px rgba(0,0,0,0.1)",
                }}
              />

              {/* THIS MONTH: teal line + gradient fill */}
              <Area
                type="monotone"
                dataKey="thisMonth"
                stroke="#0f766e"            // teal line
                strokeWidth={2}
                strokeLinecap="round"
                strokeLinejoin="round"
                fill="url(#tealFill)"        // gradient under line
                dot={false}
                activeDot={false}
              />

              {/* LAST MONTH: gray dotted line, no fill */}
              <Line
                type="monotone"
                dataKey="lastMonth"
                stroke="#94a3b8"
                strokeWidth={2}
                strokeDasharray="5 6"
                dot={false}
                activeDot={false}
              />
            </ComposedChart>
          </ResponsiveContainer>
        </div>
      </div>
    </div>
  );
}
